:root {
  --var-color-primary: #ff980d;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.about-film-modal {
  height: 100%;
  width: 100%;
  z-index: 999;
  opacity: 1;
  background-color: #0003;
  padding: 20px;
  transition: opacity .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.about-film-modal.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.about-film-modal .container {
  overflow-y: auto;
}

.modal-body {
  width: 90%;
  background-color: #fff;
  padding: 48px 20px 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -30%);
}

@media screen and (min-width: 480px) {
  .modal-body {
    width: 65%;
    padding: 48px 20px 40px;
    transform: translate(-50%, -30%);
  }
}

@media screen and (min-width: 768px) {
  .modal-body {
    width: 704px;
    padding: 40px 72px 64px 36px;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 1200px) {
  .modal-body {
    width: 806px;
    padding: 40px 12px;
    transform: translate(-50%, -50%);
  }
}

.close-btn {
  width: 40px;
  height: 40px;
  color: #000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: color .25s ease-in-out, background-color .25s ease-in-out;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close-btn:hover {
  color: #fff;
  background-color: var(--var-color-primary);
}

.close__icon {
  width: 30px;
  height: 30px;
  stroke: currentColor;
  display: block;
}

.about-film {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .about-film {
    flex-direction: row;
    align-items: flex-start;
    gap: 68px;
  }
}

@media screen and (min-width: 1200px) {
  .about-film {
    align-items: stretch;
    gap: 16px;
  }
}

.about-film__body {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.about-film__img {
  object-fit: cover;
}

@media screen and (min-width: 768px) {
  .about-film__img {
    max-width: 254px;
  }
}

@media screen and (min-width: 1200px) {
  .about-film__img {
    max-width: 375px;
  }
}

.film__title {
  text-transform: uppercase;
  font-size: 1.25rem;
  line-height: 1.15;
}

.film-atributes {
  gap: 40px;
  display: flex;
}

.film-properties, .film-value {
  color: #545454;
  flex-direction: column;
  gap: 8px;
  line-height: 1.33;
  display: flex;
}

.film-properties {
  flex-grow: 1;
  flex-shrink: 0;
}

.value {
  align-items: center;
  gap: 3px;
  display: flex;
}

.film-value {
  color: #000;
}

.value--red, .value--gray-bg {
  background: var(--var-color-primary);
  width: 36px;
  height: 16px;
  color: #fff;
  border-radius: .3125rem;
  justify-content: center;
  align-items: center;
  padding: 1px 4px;
  line-height: 1;
  display: flex;
}

.value--gray {
  color: #545454;
}

.value--gray-bg {
  color: #000;
  background: #f7f7f7;
}

.description__title {
  text-transform: uppercase;
  margin-bottom: 8px;
}

.description {
  line-height: 1.67;
}

.button__wraper {
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
}

@media screen and (min-width: 769px) {
  .button__wraper {
    gap: 8px;
  }
}

.add-to-watched, .add-to-queue {
  font-weight: inherit;
  text-transform: uppercase;
  color: #fff;
  color: var(--var-color-primary);
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 6.25rem;
  padding: 10px 12px;
}

.add-to-watched:hover, .add-to-queue:hover {
  background-color: var(--var-color-primary);
  color: #fff;
  border: none;
  padding: 11px 13px;
}

.add-to-watched:focus, .add-to-queue:focus {
  outline: none;
}

.add-to-active {
  background-color: var(--var-color-primary);
  color: #fff;
  border: none;
  padding: 11px 13px;
}

.youtube-link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  background-color: #fff;
  border-radius: 6.25rem;
  padding: 10px 22px;
  display: block;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 769px) {
  .container {
    max-width: 1280px;
  }
}

:focus {
  outline: 1px solid var(--var-color-primary);
}

* {
  box-sizing: border-box;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

a, a:visited, a:hover {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

body {
  color: #000;
  background-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: .75rem;
  font-weight: 500;
  line-height: 1.16;
}

.modal-open {
  overflow: hidden;
}

button, ul, p, h1, h2, h3, h4, h5 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

header .container {
  height: 100lvh;
  margin: 0 auto;
  padding: 20px;
  overflow: hidden;
}

@media screen and (min-width: 480px) {
  header .container {
    height: 286px;
    padding: 32px;
  }
}

@media screen and (min-width: 769px) {
  header .container {
    height: 316px;
  }
}

.header-nav {
  height: 100%;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  padding: 14px;
  display: flex;
  position: relative;
}

.logo {
  color: #fff;
  position: absolute;
  bottom: 74px;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (min-width: 480px) {
  .logo {
    bottom: 16px;
    left: 16px;
    transform: translateX(1px);
  }
}

@media screen and (min-width: 769px) {
  .logo {
    bottom: 32px;
    left: 32px;
  }
}

.logo__icon {
  width: 190px;
  height: 38px;
}

@media screen and (min-width: 480px) {
  .logo__icon {
    width: 233px;
    height: 47px;
  }
}

@media screen and (min-width: 769px) {
  .logo__icon {
    width: 375px;
    height: 75px;
  }
}

.header-nav__list-1 {
  flex-flow: wrap;
  gap: 8px 4px;
  display: flex;
}

.header-nav__list-1 :last-child {
  flex: auto;
}

@media screen and (min-width: 480px) {
  .header-nav__list-1 :last-child {
    flex: 0 auto;
  }
}

@media screen and (min-width: 480px) {
  .header-nav__list-1 {
    margin-right: auto;
  }
}

.header-nav__list-2 {
  justify-content: center;
  column-gap: 4px;
  display: flex;
}

@media screen and (min-width: 480px) {
  .header-nav__list-2 {
    margin-top: 40px;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__list-2 {
    column-gap: 30px;
    margin-top: 40px;
  }
}

.header-nav__list-2.is-hidden {
  display: none;
}

.header-nav__title {
  text-align: center;
  text-transform: uppercase;
  border-radius: 6.25rem;
  padding: 10px 22px;
  display: block;
}

.header-nav__title--home {
  color: #000;
  background-color: #fff;
  padding: 10px 26px;
}

.header-nav__title--home:hover, .header-nav__title--home:focus {
  color: #fff;
  background-color: #000;
}

@media screen and (min-width: 480px) {
  .header-nav__title--home {
    padding: 12px 24px;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__title--home {
    padding: 14px 32px;
  }
}

.header-nav__title--lib {
  color: #000;
  background-color: #fff;
  padding: 10px 26px;
}

.header-nav__title--lib:hover, .header-nav__title--lib:focus {
  color: #fff;
  background-color: #000;
}

@media screen and (min-width: 480px) {
  .header-nav__title--lib {
    padding: 12px 20px;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__title--lib {
    padding: 14px 24px;
  }
}

.header-nav__title--watch {
  color: var(--var-color-primary);
  background: #fff;
  margin-bottom: 10px;
  box-shadow: 0 0 7px #1b1b1b26;
}

.header-nav__title--watch:hover, .header-nav__title--watch:focus {
  color: #fff;
  background: var(--var-color-primary);
}

@media screen and (min-width: 480px) {
  .header-nav__title--watch {
    margin-bottom: 0;
    padding: 12px 22px;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__title--watch {
    padding: 14px 32px;
  }
}

.header-nav__title--queue {
  color: var(--var-color-primary);
  background: #fff;
  margin-bottom: 10px;
}

.header-nav__title--queue:hover, .header-nav__title--queue:focus {
  color: #fff;
  background: var(--var-color-primary);
}

@media screen and (min-width: 480px) {
  .header-nav__title--queue {
    margin-bottom: 0;
    padding: 12px 22px;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__title--queue {
    padding: 14px 32px;
  }
}

.header-nav__title--active {
  color: #fff;
  background-color: #000;
}

.header-nav__item {
  color: #000;
  position: relative;
}

@media screen and (min-width: 670px) {
  .header-nav__item--home {
    order: 2;
  }
}

@media screen and (min-width: 670px) {
  .header-nav__item--lib {
    order: 3;
  }
}

@media screen and (min-width: 670px) {
  .header-nav__item--input {
    order: 1;
  }
}

.header-nav__input {
  min-width: 250px;
  width: 100%;
  background-color: #fff;
  border: none;
  border-radius: 6.25rem;
  padding: 10px 14px;
}

.header-nav__input::placeholder {
  color: #000;
  font-weight: 400;
}

@media screen and (min-width: 480px) {
  .header-nav__input::placeholder {
    font-size: .875rem;
  }
}

@media screen and (min-width: 480px) {
  .header-nav__input {
    min-width: 105px;
    padding: 11px 16px;
    font-size: .875rem;
  }
}

@media screen and (min-width: 769px) {
  .header-nav__input {
    min-width: 187px;
    padding: 13px 16px;
  }
}

.search__icon {
  stroke: currentColor;
  fill: #0000;
  position: absolute;
  top: 10px;
  right: 14px;
}

@media screen and (min-width: 480px) {
  .search__icon {
    top: 11px;
    right: 16px;
  }
}

@media screen and (min-width: 769px) {
  .search__icon {
    top: 13px;
  }
}

.header-nav__item--input.is-hidden {
  display: none;
}

.switch-them {
  zoom: 150%;
  position: absolute;
  top: 40%;
  right: 20px;
}

@media screen and (min-width: 480px) {
  .switch-them {
    zoom: 100%;
    top: 30%;
    right: 30px;
  }
}

.switch-body {
  height: 120px;
  width: 30px;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 5px #000;
}

.switch-line {
  height: 90px;
  width: 20px;
  background-color: #f3ebe033;
  border-radius: 1rem;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: inset 0 0 3px #000;
}

.switch-orange, .switch-red, .switch-yellow {
  width: 20px;
  height: 20px;
  border: 1px solid #2525254d;
  border-radius: 1rem;
}

.switch-orange:hover, .switch-red:hover, .switch-yellow:hover {
  filter: contrast(1.5);
  width: 25px;
  height: 25px;
  box-shadow: -4px -3px 7px 3px #00000087;
}

.switch-yellow {
  background-color: #ece913;
}

.switch-red {
  background-color: #b92f2c;
}

.switch-orange {
  background-color: #ff9100;
}

.switch--active {
  width: 25px;
  height: 25px;
  box-shadow: -4px -3px 7px 3px #00000087;
}

.header-error {
  max-width: 480px;
  background: #fffc;
  border-radius: .4rem;
  margin-top: 8px;
}

@media screen and (min-width: 480px) {
  .header-error {
    max-width: 768px;
  }
}

@media screen and (min-width: 769px) {
  .header-error {
    max-width: 1280px;
  }
}

.header-error-text {
  width: 170px;
  color: #000;
  padding: 3px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.2;
  display: block;
}

@media screen and (min-width: 480px) {
  .header-error-text {
    width: 150px;
    padding: 3px;
  }
}

@media screen and (min-width: 769px) {
  .header-error-text {
    width: 170px;
    padding: 3px;
  }
}

.films-list {
  flex-wrap: wrap;
  gap: 20px;
  line-height: 1;
  display: flex;
}

@media screen and (min-width: 480px) {
  .films-list {
    gap: 32px;
  }
}

@media screen and (min-width: 800px) {
  .films-list {
    gap: 16px;
  }
}

.films-list.library-empty {
  height: 45svh;
}

.film {
  flex-basis: 100%;
}

@media screen and (min-width: 480px) {
  .film {
    flex-basis: calc(50% - 16px);
  }
}

@media screen and (min-width: 800px) {
  .film {
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

.film-link {
  color: inherit;
}

.film-link:hover {
  filter: brightness(1.3);
}

.film-link:hover .film__name {
  filter: brightness(.6);
  color: var(--var-color-primary);
}

.film-link:hover .film__description {
  color: #545454;
}

.film__img {
  object-fit: cover;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 10px;
  display: block;
}

@media screen and (min-width: 480px) {
  .film__img {
    height: 455px;
    margin-bottom: 8px;
  }
}

@media screen and (min-width: 800px) {
  .film__img {
    height: 574px;
    margin-bottom: 12px;
  }
}

.film-body {
  font-size: .875rem;
}

@media screen and (min-width: 800px) {
  .film-body {
    font-size: 1.25rem;
    line-height: 1.2;
  }
}

.film__name {
  text-transform: uppercase;
  margin-bottom: 7px;
}

.film__description {
  color: var(--var-color-primary);
}

.page-list {
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  display: flex;
}

@media screen and (min-width: 480px) {
  .page-list {
    padding: 60px 0;
  }
}

.page-lisy__item:nth-child(1), .page-lisy__item:nth-child(7) {
  background: #f7f7f7;
  border-radius: .3125rem;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page {
  color: #000;
  border-radius: .3125rem;
  padding: 13px;
  display: block;
}

.page:hover, .page:focus {
  color: #fff;
  background-color: var(--var-color-primary);
}

.page--active {
  background-color: var(--var-color-primary);
  color: #fff;
  border-radius: .3125rem;
}

.page-arrow {
  height: 14px;
  width: 14px;
  stroke: currentColor;
}

.page-arrow--right {
  transform: scaleX(-1);
}

.footer {
  background-color: #f7f7f7;
}

.footer-body {
  color: #545454;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding: 28px 0;
  font-size: .875rem;
  line-height: 1.14;
  display: flex;
}

@media screen and (min-width: 544px) {
  .footer-body {
    flex-flow: row;
    gap: 3px;
    padding: 30px 0;
    font-size: 1rem;
    line-height: 1.18;
  }
}

@media screen and (min-width: 769px) {
  .footer-body {
    padding: 29px 0;
  }
}

.footer-body__wrapper {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.description__icon {
  fill: #b92f2c;
}

.description__link {
  color: inherit;
}

.description__link span {
  text-decoration-line: underline;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.description__link span:hover {
  color: var(--var-color-primary);
}

.description__icon {
  width: 14px;
  height: 14px;
  fill: #b92f2c;
  animation: beat 1s ease-in-out infinite;
}

@keyframes beat {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

